.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: 0.8s ease;
  z-index: 2;
}

.popup_opened {
  opacity: 1;
  visibility: visible;
}

.popup__container {
  width: 358px;
  background-color: #fff;
  border-radius: 10px;
  padding: 34px 36px 37px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
}

.popup__container-tooltip {
  padding: 60px 36px 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.popup__container-img {
  width: 75vw;
  height: 75vh;
  padding: 0;
  background: transparent;
  box-shadow: none;
  position: relative;
}

@media (max-width: 550px) {
  .popup__container {
    width: 300px;
  }
}
@media (max-width: 440px) {
  .popup__container {
    padding: 25px 22px;
  }
}
@media (max-width: 400px) {
  .popup__container {
    width: 262px;
  }
}
@media (max-width: 340px) {
  .popup__container {
    width: 242px;
  }
}

.popup__close {
  width: 32px;
  height: 32px;
  background: url("../../images/Close-Icon.svg");
  position: absolute;
  background-size: cover;
  border: 0;
  top: -40px;
  right: -40px;
}
.popup__close:hover {
  cursor: pointer;
  opacity: 0.6;
}
@media (max-width: 500px) {
  .popup__close {
    width: 26px;
    height: 26px;
    right: 0px;
  }
}
@media (max-width: 400px) {
  .popup__close {
    width: 23px;
    height: 23px;
  }
}
@media (max-width: 320px) {
  .popup__close {
    width: 20px;
    height: 20px;
    top: -37px;
  }
}

.popup__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
.popup__heading {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #fff;
  margin: 0;
}

.popup__title {
  margin: 0;
  color: #000000;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
}
.popup__title-tooltip {
  text-align: center;
}
@media (max-width: 830px) and (min-width: 500px) {
  .popup__title {
    font-size: 23px;
    line-height: 26px;
  }
}
@media (max-width: 500px) and (min-width: 400px) {
  .popup__title {
    font-size: 22px;
    line-height: 24px;
  }
}
@media (max-width: 400px) and (min-width: 320px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
  }
}

.popup__img-tooltip {
  width: 120px;
  height: 120px;
  margin-bottom: 32px;
  background-image: url("../../images/successful.svg");
}
.popup__img-tooltip_eroor {
  background-image: url("../../images/error.svg");
}
